body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app {
  background-image: linear-gradient(
      to right,
      rgba(255, 255, 255, 0.9) 2%,
      rgba(255, 255, 255, 0.7) 50%,
      rgba(255, 255, 255, 0) 100%
    ),
    url(/static/media/bg.bcfbdfe0.png);

  position: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-clip: cover;
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  justify-content: flex-start;
  /* flex-direction: row; */
}

.scrollable {
  background: rgba(255, 255, 255, 0.9);
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  position: absolute;
  top: 91vh;
  width: 650px;
  margin-left: 50%;
  margin-right: 50px;
  text-align: center;
  transition: all 0.3s ease-in;
}

.scrollable:hover {
  background: #ffffff;
  transition: all 0.3s ease-in;
}

.scrollable .chevron {
  cursor: pointer;
  margin-top: 20px;
  position: relative;
  -webkit-animation: MoveUpDown 1.3s linear infinite;
          animation: MoveUpDown 1.3s linear infinite;
}

@-webkit-keyframes MoveUpDown {
  0%,
  100% {
    top: 5px;
  }

  50% {
    top: 15px;
  }
}

@keyframes MoveUpDown {
  0%,
  100% {
    top: 5px;
  }

  50% {
    top: 15px;
  }
}

.scrollable h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #35a6de;
}

p {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
}

.scrollable .profile {
  display: flex;
  margin-left: 25px;
  margin-right: 25px;
  align-items: center;
}

.scrollable .profile img {
  width: 110px;
  height: 110px;
  margin-left: 15px;
  flex-shrink: 0;
  /* border-radius: 55px;
  -webkit-border-radius: 55px;
  -moz-border-radius: 55px; */
}

.scrollable .profile .summary {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  margin-left: 30px;
}

.scrollable h2 {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  font-style: normal;
  margin-left: 25px;
  margin-bottom: 20px;
}

.scrollable .about em {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #35a6de;
  text-align: left;
  font-style: normal;
}

.scrollable .skills {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  margin-left: 25px;
  margin-right: 25px;
}

.scrollable .skills em::before {
  content: "• ";
}

.scrollable .skills .left {
  margin-left: 15px;
  flex: 1 1;
}

.scrollable .skills .list {
  flex: 2.5 1;
}

.scrollable .skills .list .languages em {
  font-weight: 800;
}

.scrollable .skills .list .frameworks em {
  font-weight: 600;
}

.scrollable .skills .list .software em {
  font-weight: 400;
}

@-webkit-keyframes strikein {
  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }

  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@keyframes strikein {
  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }

  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}

@-webkit-keyframes strikeout {
  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

@keyframes strikeout {
  50% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

  0% {
    -webkit-transform-origin: 0% 50%;
            transform-origin: 0% 50%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
}

.strike {
  position: relative;
  -webkit-animation: strikein 0.75s ease-in-out forwards;
          animation: strikein 0.75s ease-in-out forwards;
}

.strike:hover:after {
  content: " ";
  position: absolute;
  top: 90%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #35a6de;
  -webkit-animation: strikein 0.75s ease-in-out forwards;
          animation: strikein 0.75s ease-in-out forwards;
}

