    border-style: solid;
    border-width: 2px;
    border-color: rgba(128, 128, 128, 0.3);
    margin-top: 8px;
    transition: all .3s ease-out;
}

.column img:hover {
    border-color: #35a6de;
    transition: all .3s ease-in;
}

