.experience {
  border-top: 1px solid rgba(128, 128, 128, 0.3);
  margin-left: 25px;
  margin-right: 25px;
  margin-top: 15px;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  padding-bottom: 15px;
}

.experience h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  color: #35a6de;
  text-align: left;
  margin-left: 15px;
  margin-top: 20px;
}

.experience .job {
  padding-left: 15px;
  margin-right: 15px;
  display: flex;
  flex-direction: column;
}

.experience .job .heading {
  display: flex;
  justify-content: flex-start;
}

.experience .job .heading h3 {
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
  font-weight: 700;
  color: #333333;
  text-align: left;
  float: left;
  margin-top: 0px;
}

.experience .job .heading h3 a {
  text-decoration: none;
  color: #333333;
}

.experience .job .heading h4 {
  font-family: 'Open Sans', sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #333333;
  text-align: left;
  color: #35a6de;
  margin-top: 0;
  margin-left: 10px;
  /* padding-right: 15px; */
}

.experience .job .heading h4::before {
  content: "• ";
  color: #35a6de;
}

.experience .job h5 {
  margin-top: -20px;
  align-self: flex-start;
  font-family: 'Open Sans', sans-serif;
  font-size: 0.85rem;
  font-weight: 500;
  color: #333333;
}

.experience .job p {
  font-family: 'Open Sans', sans-serif;
  font-size: 0.95rem;
  font-weight: 400;
  color: #333333;
  text-align: left;
  margin-top: -10px;
}

.experience .job ul {
  list-style: none;
  margin-top: 25px;
  font-family: 'Open Sans', sans-serif;
  font-size: 0.95rem;
  font-weight: 400;
  color: #333333;
  text-align: left;
  margin-top: -10px;
}

.experience .job ul li {
  list-style-position: outside;
  margin-left: -1.5em;
}

.experience .job li::before {
  content: "• ";
  color: #35a6de;
}

@media screen and (max-width: 600px) {

  .experience .job {
    margin-left: 30px;
  }

  .experience .job ul {
    font-size: 0.95rem;
  }

}

@media screen and (max-width: 555px) {

  .experience .job .heading h3 {
    font-size: 0.95rem;
  }

  .experience .job .heading h4 {
    font-size: 0.95rem;
    padding-right: 10px;
  }

  .experience .job h5 {
    font-size: 0.82rem;
  }

  .experience .job ul {
    font-size: 0.85rem;
  }
}

@media screen and (max-width: 485px) {

  .experience {
    margin-left: 0;
    margin-right: 0;
  }

  .experience .job {
    margin-left: 0px;
  }

  .experience .job .heading h3 {
    font-size: 0.85rem;
  }

  .experience .job .heading h4 {
    font-size: 0.85rem;
    margin-left: 10px;
    padding-right: 30px;
  }

  .experience .job h5 {
    font-weight: 100;
  }

  .experience .job ul {
    font-size: 0.8rem;
    margin-top: -10px;
    margin-left: -5px;
    margin-right: -15px;
  }

}

@media screen and (max-width: 430px) {

  .experience .job .heading h3 {
    font-size: 0.9rem;
    font-weight: 700;
  }

  .experience .job .heading {
    flex-direction: column;
    justify-content: flex-start;
  }

  .experience .job .heading h4 {
    margin-left: 0px;
    margin-top: -10px;
    padding-bottom: 5px;
  }

  .experience .job ul {
    font-size: 0.83rem;
    margin-left: -15px;
    margin-right: 5px;
  }

}

@media screen and (max-width: 375px) {

  .experience {
    margin-left: 0px;
    margin-right: 0px;
  }

  /* .experience .job .heading h3 {
    font-size: 0.8rem;
  }

  .experience .job .heading h4 {
    font-size: 0.8rem;
  }

  .experience .job h5 {
    font-size: 0.75rem;
    margin-top: -35px;
  } */

  .experience .job ul {
    margin-top: -10px;
  }

  .experience .job li {
    display: block;
    white-space: normal;
  }

}